@font-face {
    font-family: 'CWDangamAsac-Bold';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/CWDangamAsac-Bold.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Uiyeun';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2105@1.1/Uiyeun.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

#intro {width: 100%; height: 100%; overflow: hidden; background-color: #fff; display: flex; flex-direction: column; justify-content: center; align-items: center;}
#intro .img_animation {width: 20%; overflow: hidden; margin-bottom: 60px;}
#intro .img_animation .img_wrap {width: 100%;}
#intro .img_animation .img_wrap > img {width: 100%; height: auto;}
.img_animation {
    perspective: 1000px; /* 깊이감 부여 */
  }
  .weather-icon {
    display: block;
    transform-style: preserve-3d; /* 3D 회전 허용 */
  }
#intro .txt_box {display: flex; flex-direction: column; align-items: center; gap: 2px;}
#intro .txt_box h2{ font-family: 'CWDangamAsac-Bold',sans-serif; font-size: 50px; line-height: normal; color: #72bbff; }
#intro .txt_box p { font-family: 'Uiyeun',sans-serif; font-size: 20px; letter-spacing: -0.165px; color: #004374;}
#intro .btn_wrap {width: 30%; border-radius: 6px; overflow: hidden; margin-top: 60px;}
#intro .btn_wrap > button {width: 100%;padding: 10px; box-sizing: border-box; text-align: center; color: #fff; background-color: #72bbff;}
